
@import '../less/github-markdown.css';
#Topic {
  background: #fff;
  width: 100%;
  font-size: 0.28rem;
  height: 100%;
}
#Topic .head {
  position: fixed;
  width: 100%;
  opacity: 1;
  transition: 1s;
  z-index: 9999999;
}
#Topic .scrolltop {
  opacity: 0.8;
  transition: 1s;
}
#Topic .scrolltop .authorInfo {
  height: 0.6rem;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  transition: 1s;
}
#Topic .scrolltop .authorInfo img {
  width: 0.5rem;
  height: 0.5rem;
  transition: 1s;
}
#Topic .scrolltop .authorInfo .info {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  height: 0.5rem;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  display: none;
  transition: 5s;
}
#Topic h4 {
  font-size: 0.32rem;
  text-align: center;
  background-color: #FDDF6D;
  padding: 0;
  color: #1e1e1e;
  padding: 0 0.3rem;
}
#Topic .authorInfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #FDDF6D;
  height: 1.1rem;
  margin-bottom: 1rem;
  transition: 1s;
}
#Topic .authorInfo img {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  transition: 1s;
}
#Topic .authorInfo .info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 1rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #8a8a8a;
  transition: 1s;
  text-align: center;
}
#Topic .authorInfo .infoname {
  font-size: 0.2rem;
}
#Topic .authorInfo .infotime {
  font-size: 0.2rem;
}
#Topic .authorInfo .infopeople {
  font-size: 0.2rem;
}
#Topic .article {
  width: 90%;
  margin: 0 auto;
  padding-bottom: 1rem;
  color: #1e1e1e;
  font-size: 0.24rem;
  padding-top: 2.5rem;
}
#Topic .reply {
  font-size: 0.24rem;
  margin: 0 auto;
  width: 100%;
  height: 100%;
}
#Topic .reply h3 {
  width: 100%;
  margin: 0 auto;
  padding: 0.4rem 0;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
#Topic .reply h3 strong {
  font-weight: 800;
  color: #000;
}
#Topic .reply ul {
  padding-left: 0;
  width: 100%;
  height: 100%;
}
#Topic .reply li {
  width: 100%;
  border-bottom: 1px solid #ccc;
  margin: 0 auto;
  height: 100%;
  padding: 0.2rem 0;
}
#Topic .reply .info {
  position: relative;
  height: 2rem;
  height: 100%;
}
#Topic .reply .infoimg {
  padding: 0rem 5% 0 5%;
  display: inline-block;
}
#Topic .reply .infoimg p {
  text-align: center;
  font-size: 0.18rem;
  color: #ccc;
}
#Topic .reply .infoimg img {
  border-radius: 50%;
  width: 0.8rem;
  height: 0.8rem;
}
#Topic .reply .infoall {
  width: 74%;
  display: inline-block;
}
#Topic .reply .infoname {
  font-size: 0.24rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#Topic .reply .infouse {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#Topic .reply .infouse img {
  width: 0.3rem;
  height: 0.3rem;
}
#Topic .comment {
  width: 94%;
  margin: 0 auto;
  border: 1px solid #FDDF6D;
}
#Topic .commenttext .mint-field-core {
  font-size: 0.32rem !important;
}
#Topic .commentbtn {
  width: 100%;
  font-size: 0.32rem;
  height: 0.8rem;
  background-color: #FDDF6D;
}



