
#Publish {
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding-bottom: 1.5rem;
}
#Publish .head {
  height: 0.8rem;
  background: #FDDF6D;
}
#Publish .head .mint-header-title {
  font-size: 0.36rem;
}
#Publish .mint-cell-value .mint-field-core {
  font-size: 0.32rem !important;
  border: 1px solid #FDDF6D;
  border-radius: 5px;
  margin-top: 0.3rem;
  font-size: 0.32rem;
  padding: 0.2rem 0;
}
#Publish .selecttitle {
  font-size: 0.32rem;
  text-align: center;
}
#Publish .picker {
  /*background-color:#FDDF6D;*/
  text-align: center;
  margin: 0 auto;
}
#Publish .picker .picker-item {
  font-size: 0.32rem;
}
#Publish .picker .picker-selected {
  color: #FDDF6D;
}
#Publish .content {
  border: 1px #FDDF6D;
}
#Publish .content {
  margin-bottom: 1rem;
}
#Publish .addbtn {
  width: 100%;
  background: #FDDF6D;
  border: 0;
  font-size: 0.32rem;
  height: 0.8rem;
  margin-top: 0 !important;
  padding-top: 0 !important;
}
#Publish .addtitle {
  width: 100%;
  text-align: center;
  font-size: 0.38rem;
  background: #FDDF6D;
  padding: 0.26rem 0;
  color: #8a8a8a;
}
#Publish .mint-toast-text {
  font-size: 0.32rem;
}



