
body,
div,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
form,
input,
textarea,
p {
  margin: 0px;
  padding: 0px;
  font-family: 微软雅黑;
}
ul {
  list-style: none;
}
img,
a img {
  border: none;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  color: #8a8a8a;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
a:hover {
  text-decoration: none;
}
input {
  -webkit-appearance: none;
}
button {
  -webkit-tap-highlight-color: transparent;
}
body,
html,
#app {
  width: 100%;
  height: 100%;
}
.top {
  color: red;
  font-size: 0.18rem;
}
.good {
  color: #FDDF6D;
  font-size: 0.18rem;
}
#Tabbar a {
  color: #8a8a8a;
  font-size: 0.3rem;
  background-color: #fff !important;
}
.mint-tab-item .mint-tab-item-icon {
  width: 0.40rem !important;
  height: 0.40rem !important;
}
.mint-tab-item-label {
  font-size: 0.32rem !important;
}



