
#Login {
  width: 100%;
  background-color: #fff;
  margin: 0 auto;
}
#Login .login {
  margin: 0 auto;
  width: 90%;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
#Login .loginbtn {
  width: 100%;
  background-color: #FDDF6D;
  font-size: 0.32rem;
  height: 1rem;
  margin: 0 auto;
}
#Login .loginname {
  margin: 0 auto;
  border: 1px solid #FDDF6D;
  font-size: 0.62rem !important;
  width: 100%;
  height: 1rem;
  color: #FDDF6D;
}
#Login .loginname .mint-field-core {
  font-size: 0.32rem !important;
}
.mint-toast-text {
  font-size: 0.32rem;
}
#Login .loginname .mintui-field-error {
  font-size: 0.32rem;
  color: #888;
}



