
body,
div,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
form,
input,
textarea,
p {
  margin: 0px;
  padding: 0px;
  font-family: 微软雅黑;
}
ul {
  list-style: none;
}
img,
a img {
  border: none;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  color: #8a8a8a;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
a:hover {
  text-decoration: none;
}
input {
  -webkit-appearance: none;
}
button {
  -webkit-tap-highlight-color: transparent;
}
body,
html,
#app {
  width: 100%;
  height: 100%;
}
.top {
  color: red;
  font-size: 0.18rem;
}
.good {
  color: #FDDF6D;
  font-size: 0.18rem;
}
body {
  background: url(assets/gif/login4.gif) no-repeat center #fff;
  background-size: 1.2rem 1.2rem;
  height: 100%;
  width: 100%;
  line-height: 1;
}



