
#About {
  width: 100%;
  height: 100%;
  background-color: #fff;
}
#About .btn li {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  height: 1rem;
  position: relative;
}
#About .about {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#About .about .imgabout {
  position: absolute;
  top: 50%;
  left: 5%;
  margin-top: -0.5rem;
  width: 0.6rem;
  height: 0.6rem;
}
#About .about .imgabout img {
  width: 0.6rem;
  height: 0.6rem;
  /*vertical-align:middle*/
}
#About .about .textabout {
  font-size: 0.4rem;
  position: absolute;
  left: 17%;
  top: 50%;
  margin-top: -0.15rem;
  color: #1e1e1e;
  /*vertical-align:middle*/
}
#About .about .imgright img {
  width: 0.6rem;
  height: 0.6rem;
}
#About .about .imgright {
  position: absolute;
  width: 0.6rem;
  height: 0.6rem;
  top: 50%;
  right: 0;
  margin-top: -0.5rem;
}
#About .popup {
  width: 80%;
  height: 100%;
  color: #1e1e1e;
}
#About .content {
  height: 100%;
  width: 100%;
  font-size: 0.32rem;
}
#About .content h3 {
  text-align: center;
  padding-top: 0.2rem;
  font-size: 0.42rem;
  padding-bottom: 0.2rem;
  width: 95%;
  margin: 0 auto;
  color: #FDDF6D;
  border-bottom: 1px solid #ccc;
}
#About .content ul li {
  font-size: 0.28rem;
  width: 95%;
  margin: 0 auto;
  border-bottom: 1px solid #ccc;
  padding-top: 0.2rem;
  text-align: center;
  padding-bottom: 0.2rem;
}
#About .content ul li h4 {
  padding-bottom: 0.2rem;
  text-align: center;
  font-size: 0.32rem;
}
#About .content ul li p {
  line-height: 0.32rem;
}
#About .content ul li a {
  color: #FDDF6D;
}



