
#Message {
  width: 100%;
  height: 100%;
  background-color: #fff;
}
#Message .is-selected {
  color: #FDDF6D !important;
  border-color: #FDDF6D;
}
#Message .message {
  position: relative;
  height: 1.4rem;
  font-size: 0.32rem;
  width: 100%;
  border-bottom: 1px solid #ccc;
}
#Message .messageimg {
  position: absolute;
  left: 0.3rem;
  margin-top: -0.4rem;
  top: 50%;
}
#Message .messageimg img {
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
}
#Message .message .messagecontent {
  position: absolute;
  width: 78%;
  left: 20%;
  height: 0.8rem;
  font-size: 0.24rem;
  top: 50%;
  margin-top: -0.4rem;
}
#Message .message .title {
  font-size: 0.32rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #1e1e1e;
}
#Message .message .nametime {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}



