
body,
div,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
form,
input,
textarea,
p {
  margin: 0px;
  padding: 0px;
  font-family: 微软雅黑;
}
ul {
  list-style: none;
}
img,
a img {
  border: none;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  color: #8a8a8a;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
a:hover {
  text-decoration: none;
}
input {
  -webkit-appearance: none;
}
button {
  -webkit-tap-highlight-color: transparent;
}
body,
html,
#app {
  width: 100%;
  height: 100%;
}
.top {
  color: red;
  font-size: 0.18rem;
}
.good {
  color: #FDDF6D;
  font-size: 0.18rem;
}
#User {
  width: 100%;
  height: 100%;
  background: #fff;
  font-size: 0.32rem;
}
#User .header {
  width: 100%;
  background-color: #FDDF6D;
}
#User h3 {
  text-align: center;
  padding: 0.1rem 0;
}
#User .userimg {
  border-radius: 50%;
  margin: 0 auto;
  width: 2rem;
  height: 2rem;
  display: block;
}
#User .username {
  text-align: center;
  padding: 0.1rem 0;
}
#User .headertext {
  width: 100%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
#User .listitem {
  background-color: #FDDF6D;
  color: #ccc;
}
#User .list {
  margin-top: 0.05rem;
}
#User .list .mint-cell {
  height: 1rem;
  border: 5px;
}
#User .list span {
  font-size: 0.62rem;
}
#User .list img {
  border-radius: 50%;
  width: 0.8rem;
  height: 0.8rem;
}
#User li {
  height: 1.4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
#User .listimg {
  position: absolute;
  top: 50%;
  margin-top: -0.4rem;
  left: 5%;
}
#User .listcontent {
  width: 70%;
  font-size: 0.30rem;
  position: absolute;
  left: 25%;
  height: 0.9rem;
  top: 50%;
  margin-top: -0.45rem;
}
#User .listcontent .title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #1e1e1e;
}
#User .listcontent .nametime {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding-top: 0.1rem;
}
#User .listcontent span {
  font-size: 0.24rem;
}
.mint-toast-text {
  font-size: 0.32rem;
}



